<template>
  <div class="wrapper">
    <Navbar />
    <section class="main-content section">
      <div class="container">
        <nuxt />
      </div>
    </section>
    <Footer />
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'

export default {
  components: {
    Navbar,
    Footer
  },
  mounted() {
    this.$preventMultitabs()
    if (process.browser) {
      window.onNuxtReady(() => {
        setTimeout(async () => {
          // eslint-disable-next-line no-console
          console.log('run!')
          try {
            const { ethAccount } = await this.$store.dispatch('metamask/askPermission')
            if (ethAccount) {
              this.$store.dispatch('txStorage/setupAccount')
              await this.$store.dispatch('distribution/fetchFee')
              await this.$store.dispatch('distribution/fetchVipPrices')
              await this.$store.dispatch('metamask/fetchTokenBalances', {})
              this.$store.dispatch('txStorage/rerunPendingTxsWatcher')
            }
            this.$store.dispatch('metamask/fetchGasPrice', {})
          } catch (e) {
            // TODO show error message
            // eslint-disable-next-line no-console
            console.log(e)
          }
        }, 500)
      })
    }
  }
}
</script>
