/* eslint-disable no-console */
import txStatus from './txStatus'
const { hexToNumber } = require('web3-utils')

export const state = () => ({
  netId1: {
    accountSender: {},
    txs: {}
  },
  netId3: {
    accountSender: {},
    txs: {}
  },
  netId4: {
    accountSender: {},
    txs: {}
  },
  netId5: {
    accountSender: {},
    txs: {}
  },
  netId42: {
    accountSender: {},
    txs: {}
  },
  netId77: {
    accountSender: {},
    txs: {}
  },
  netId99: {
    accountSender: {},
    txs: {}
  },
  netId100: {
    accountSender: {},
    txs: {}
  },
  netId333: {
    accountSender: {},
    txs: {}
  }
})

export const getters = {
  accountSender: (state, getters, rootState, rootGetters) => {
    const netId = rootGetters['metamask/netId']
    return state[`netId${netId}`].accountSender
  },
  txs: (state, getters, rootState, rootGetters) => {
    const netId = rootGetters['metamask/netId']
    return state[`netId${netId}`].txs
  },
  txsToRender: (state, getters) => {
    const txs = Object.entries(getters.txs).map(([txHash, tx]) => {
      return {
        txHash,
        status: tx.status
      }
    })
    return txs
  },
  allTxsHaveSuccess: (state, getters) => {
    const txs = Object.entries(getters.txs)
    if (txs.length === 0) return false
    const successes = txs.filter(([txHash, tx]) => {
      return tx.status === txStatus.success
    })

    return successes.length === txs.length
  },
  allTxsMined: (state, getters) => {
    const txs = Object.entries(getters.txs)
    if (txs.length === 0) return false
    const successes = txs.filter(([txHash, tx]) => {
      return [txStatus.success, txStatus.fail].includes(tx.status)
    })

    return successes.length === txs.length
  }
}

export const mutations = {
  SAVE_TX(state, { params, gas, total, txHash, isMetamask = false }) {
    const status = txStatus.waitingForReciept
    const netId = this._vm['metamask/netId']
    this._vm.$set(state[`netId${netId}`].txs, txHash, { params, gas, total, status, isMetamask })
  },
  CHANGE_TX_STATUS(state, { status, txHash }) {
    const netId = this._vm['metamask/netId']
    const tx = state[`netId${netId}`][txHash]
    this._vm.$set(state[`netId${netId}`].txs, txHash, { status, ...tx })
  },
  SET_ACCOUNT_SENDER(state, { address, privateKey }) {
    const netId = this._vm['metamask/netId']
    this._vm.$set(state[`netId${netId}`], 'accountSender', { address, privateKey })
  },
  SAVE_SENDER_SIGNATURE(state, { signature, expiration }) {
    const netId = this._vm['metamask/netId']
    const accountSender = state[`netId${netId}`].accountSender
    this._vm.$set(state[`netId${netId}`], 'accountSender', {
      signature,
      expiration,
      ...accountSender
    })
  },
  RESET_TXS_STORAGE(state) {
    const netId = this._vm['metamask/netId']
    this._vm.$set(state[`netId${netId}`], 'txs', {})
  },
  RESET_ACCOUNT_SENDER(state) {
    const netId = this._vm['metamask/netId']
    this._vm.$set(state[`netId${netId}`], 'accountSender', {})
  }
}

export const actions = {
  async runTxWatcher({ commit, dispatch }, { txHash }) {
    let { status } = await dispatch('metamask/waitForTxReceipt', { txHash }, { root: true })
    status = hexToNumber(status) === 1 ? txStatus.success : txStatus.fail
    commit('CHANGE_TX_STATUS', { status, txHash })
    return status === txStatus.success
  },
  setupAccount({ getters, commit, rootGetters }) {
    if (!getters.accountSender.address) {
      const { address, privateKey } = rootGetters['metamask/web3'].eth.accounts.create()
      commit('SET_ACCOUNT_SENDER', {
        address,
        privateKey
      })
      commit('RESET_TXS_STORAGE')
    }
  },
  async runSenderKeyDestroyer({ dispatch, commit }, { txsPromisesBucket }) {
    await Promise.all(txsPromisesBucket)
    await dispatch('distribution/sendEthLeftOversToUser', {}, { root: true })
    commit('RESET_ACCOUNT_SENDER')
  },
  async rerunPendingTxsWatcher({ dispatch, getters, commit }) {
    const txsPromisesBucket = []
    for (const txHash in getters.txs) {
      console.log(getters.txs[txHash])
      if (getters.txs[txHash].isMetamask) {
        commit('RESET_ACCOUNT_SENDER')
        commit('RESET_TXS_STORAGE')
        dispatch('setupAccount')
      } else {
        const receiptStatusPromise = dispatch('runTxWatcher', { txHash })
        txsPromisesBucket.push(receiptStatusPromise)
      }
    }

    if (txsPromisesBucket.length > 0) {
      await dispatch('runSenderKeyDestroyer', { txsPromisesBucket })
      dispatch('setupAccount')
    }
  }
}
