/* eslint-disable no-unreachable */
/* eslint-disable no-console */
import Web3 from 'web3'
import parse from 'csv-parse/lib/sync'
import MultisenderABI from '../abis/Multisender.abi.json'

const { hexToNumberString, toChecksumAddress, numberToHex, randomHex } = require('web3-utils')
const BN = require('bignumber.js')

const BLOCK_GAS_LIMIT = 7200000

const state = () => {
  return {
    rawList: '',
    list: [],
    totalTokens: '',
    customerFee: 0,
    txs: [],
    vipCost: [],
    strategy: 'push',
    badContributors: [],
    listErrors: [],
    isAlreadyPressed: false,
    currentCheck: {
      from: null,
      to: null,
      method: ''
    }
  }
}

const mutations = {
  SET_RAWLIST(state, rawList) {
    state.rawList = rawList
  },
  SAVE_LIST(state, list) {
    state.list = list
  },
  SAVE_TOTAL_TOKENS(state, totalTokens) {
    state.totalTokens = totalTokens
  },
  SET_CUSTOMER_FEE(state, customerFee) {
    state.customerFee = customerFee
  },
  PUSH_TX(state, tx) {
    state.txs.push(tx)
  },
  RESET_TXS(state) {
    state.txs = []
  },
  REMOVE_LAST_TX(state) {
    state.txs.pop()
  },
  SAVE_VIP_PRICE(state, { tier, price }) {
    this._vm.$set(state.vipCost, tier, price)
  },
  SET_STRATEGY(state, strategy) {
    state.strategy = strategy
  },
  SAVE_BAD_CONTRIBUTORS(state, badContributors) {
    state.badContributors = badContributors
  },
  REMOVE_ADDRESSES_FROM_LIST(state) {
    const badContributors = state.badContributors.map(badContributor => {
      return badContributor.address
    })
    const filteredAddresses = state.list.reduce((accumulator, entry) => {
      if (!badContributors.includes(entry[0])) {
        accumulator.push([entry[0], entry[1]])
      }
      return accumulator
    }, [])
    state.list = filteredAddresses
  },
  SAVE_ERROR(state, error) {
    state.listErrors.push(error)
  },
  CLEAN_ERRORS(state) {
    state.listErrors = []
  },
  SET_ALREADY_PRESSED(state, status) {
    state.isAlreadyPressed = status
  },
  SAVE_CURRENT_CHECK(state, { from, to, method }) {
    state.currentCheck = {
      from,
      to,
      method
    }
  }
}

const getters = {
  multisenderInstance: (state, getters, rootState, rootGetters) => {
    const web3 = rootGetters['metamask/web3']
    const { multisenderContractAddress } = rootGetters['metamask/networkConfig']
    return new web3.eth.Contract(MultisenderABI, multisenderContractAddress)
  },
  hasEnoughApprovedTokens(state, getters, rootState) {
    return new BN(rootState.token.allowance).gte(new BN(state.totalTokens))
  },
  approximateTxCost: (state, getters, rootState, rootGetters) => {
    let totalGas = state.txs.reduce((accumulator, tx) => {
      accumulator += tx.gas
      return accumulator
    }, 0)
    totalGas = new BN(totalGas)
    const gasPrice = new BN(rootGetters['metamask/gasPrice'])
    const customerFee = new BN(state.customerFee).multipliedBy(state.txs.length)
    return totalGas
      .multipliedBy(gasPrice)
      .plus(customerFee)
      .toString()
  },
  totalEthToSend: (state, getters, rootState, rootGetters) => {
    const isNative = rootGetters['token/isNative']
    if (isNative) {
      const totalEth = state.txs.reduce((accumulator, tx) => {
        accumulator = accumulator.plus(tx.total)
        return accumulator
      }, BN(0))
      return totalEth.plus(BN(getters.approximateTxCost)).toString()
    } else {
      return getters.approximateTxCost
    }
  },
  airDropList: state => (start = 0, end = Infinity) => {
    console.log(start, end)
    return state.list.slice(start, end).reduce(
      (list, entry) => {
        list.addresses.push(entry[0])
        list.balances.push(entry[1])
        list.total = list.total.plus(entry[1])
        return list
      },
      { addresses: [], balances: [], total: new BN(0) }
    )
  },
  extraGas: () => {
    return 100000
  }
}

const actions = {
  async checkList({ state, commit, rootState, dispatch }, { editor }) {
    let errors = false
    commit('CLEAN_ERRORS')
    commit('SET_ALREADY_PRESSED', true)
    if (editor.markers) {
      editor.markers.forEach(marker => marker.clear())
    }
    editor.markers = []
    try {
      const { decimals } = rootState.token
      if (!decimals) {
        throw new Error('Please select token first')
      }
      let total = new BN('0')
      const records = parse(state.rawList, {
        ltrim: true,
        rtrim: true,
        skip_empty_lines: true,
        trim: true,
        cast: (value, context) => {
          try {
            let decimalsBN = new BN('0')
            if (value[0] === '0' && value[1] === 'x') {
              if (value === '0x0000000000000000000000000000000000000000') {
                throw new Error('Wrong address')
              }
              return toChecksumAddress(value)
            } else {
              const amount = value.split('.')
              if (amount.length > 2) {
                throw new Error('wrong amount')
              }
              if (amount[0] === '') {
                throw new Error('wrong amount')
              }
              if (amount[1]) {
                if (amount[1].length > decimals) {
                  throw new Error('wrong decimal amount')
                }
                decimalsBN = new BN('10')
                  .pow(new BN(decimals - amount[1].length))
                  .times(new BN(amount[1].toString()))
              }
              const amountBN = new BN('10')
                .pow(new BN(decimals.toString()))
                .times(new BN(amount[0]))
                .plus(decimalsBN)
              total = total.plus(amountBN)
              return amountBN.toString(10)
            }
          } catch (e) {
            errors = true
            const marker = editor.markText(
              { line: context.records - 1 },
              { line: context.records },
              {
                className: 'cm-error',
                clearOnEnter: false,
                addToHistory: true
              }
            )
            editor.markers.push(marker)
            commit('SAVE_ERROR', `Line: ${context.records + 1} ${e.message}`)
          }
        }
      })
      if (errors) {
        commit('SET_ALREADY_PRESSED', false)
        return false
      }
      if (records.length < 2) {
        throw new Error('Please provide at least 2 addresses')
      }
      commit('SAVE_LIST', records)
      commit('SAVE_TOTAL_TOKENS', total.toString(10))
      const hasApproved = new BN(rootState.token.allowance).gte(total)
      let nextPage = 'approve'
      if (hasApproved) {
        const areBadAddresses = await dispatch('findBadAddresses', {})
        if (areBadAddresses) {
          nextPage = 'badAddresses'
        } else {
          nextPage = 'summary'
          await dispatch('prepareTxs')
        }
      }
      dispatch('router/setNextPageAndGo', { nextPage }, { root: true })
      return true
    } catch (e) {
      console.error(e)
      commit('SET_ALREADY_PRESSED', false)
      commit('SAVE_ERROR', `${e.message}`)
      return false
    }
  },
  askMultisender({ dispatch, getters }, { method, params = [], web3Method, value, gas, from }) {
    const { multisenderInstance } = getters
    const data = multisenderInstance.methods[method](...params).encodeABI()
    try {
      return dispatch(
        'metamask/callWeb3',
        { data, to: multisenderInstance.address, web3Method, value, gas, from },
        { root: true }
      )
    } catch (e) {
      throw Error(e)
    }
  },
  async fetchFee({ rootState, dispatch, commit }) {
    const { ethAccount } = rootState.metamask
    const customerFee = await dispatch('askMultisender', {
      method: 'currentFee',
      params: [ethAccount],
      web3Method: 'call'
    })
    commit('SET_CUSTOMER_FEE', hexToNumberString(customerFee))
  },
  async fetchVipPrices({ dispatch, commit, rootGetters }) {
    const web3 = rootGetters['metamask/web3']
    let prices = await dispatch('askMultisender', {
      method: 'getAllVipPrices',
      web3Method: 'call'
    })
    prices = web3.eth.abi.decodeParameters(['uint256', 'uint256', 'uint256'], prices)
    commit('SAVE_VIP_PRICE', { tier: 0, price: prices[0].toString(10) })
    commit('SAVE_VIP_PRICE', { tier: 1, price: prices[1].toString(10) })
    commit('SAVE_VIP_PRICE', { tier: 2, price: prices[2].toString(10) })
  },
  async organiseAddresses({ state, getters, commit, dispatch }) {
    const { addresses, balances } = getters.airDropList()
    const currentBalances = await dispatch('exploreBalances', { addresses })
    const freshAddressesList = []
    const existingAddressesList = currentBalances.reduce((accumulator, balance, index) => {
      if (balance.isZero()) {
        freshAddressesList.push([addresses[index], balances[index]])
      } else {
        accumulator.push([addresses[index], balances[index]])
      }
      return accumulator
    }, [])
    return {
      freshAddressesList,
      existingAddressesList
    }
  },
  async multisendWithSignature(
    { state, rootState, rootGetters, dispatch, commit, getters },
    { retryAttempt = 0 }
  ) {
    const txsPromisesBucket = []
    retryAttempt++
    try {
      const accountSender = rootGetters['txStorage/accountSender']
      const { expiration, signature, address, privateKey } = accountSender
      const method = 'getApprover'
      const params = [expiration, signature]
      let approver = await dispatch('askMultisender', {
        method,
        params,
        web3Method: 'call',
        from: address
      })
      const web3 = rootGetters['metamask/web3']
      approver = web3.eth.abi.decodeParameter('address', approver)
      if (approver !== rootState.metamask.ethAccount) {
        throw Error('signature is invalid')
      }
      const isNative = rootGetters['token/isNative']
      const { multisenderInstance } = getters
      const gasPrice = rootGetters['metamask/gasPrice']
      const account = web3.eth.accounts.wallet.add(privateKey)
      let nonce = 0
      const { netId } = rootState.metamask
      // eslint-disable-next-line prefer-const
      for (let { params, gas, total } of state.txs) {
        let data
        if (isNative) {
          data = multisenderInstance.methods.multisendEther(...params).encodeABI()
        } else {
          params = params.concat([signature, expiration])
          data = multisenderInstance.methods.multisendTokenWithSignature(...params).encodeABI()
        }

        const airDropETHValue = isNative ? total : 0
        const tx = await account.signTransaction({
          to: multisenderInstance.address,
          gas: numberToHex(gas + getters.extraGas),
          gasPrice,
          value: numberToHex(new BN(state.customerFee).plus(airDropETHValue).toString(10)),
          data,
          chainId: netId,
          nonce
        })
        nonce++
        const callParams = {
          method: 'eth_sendRawTransaction',
          params: [tx.rawTransaction],
          from: address
        }
        const txHash = await dispatch('metamask/sendAsync', callParams, {
          root: true
        })
        commit('txStorage/SAVE_TX', { txHash, params, gas, total }, { root: true })
        const receiptStatusPromise = dispatch('txStorage/runTxWatcher', { txHash }, { root: true })
        txsPromisesBucket.push(receiptStatusPromise)
      }
      dispatch('txStorage/runSenderKeyDestroyer', { txsPromisesBucket }, { root: true })
    } catch (e) {
      if (retryAttempt < 3) {
        dispatch('multisendWithSignature', { retryAttempt })
      } else {
        console.error('_multisendWithSignature', e)
        dispatch('txStorage/runSenderKeyDestroyer', { txsPromisesBucket }, { root: true })
      }
    }
  },
  sendEthLeftOversToUser({ commit, dispatch, rootState, rootGetters }) {
    window.onbeforeunload = null
    const web3 = rootGetters['metamask/web3']
    const gasPrice = rootGetters['metamask/lowGasPrice']
    const { address, privateKey } = rootGetters['txStorage/accountSender']
    const account = web3.eth.accounts.wallet.add(privateKey)
    const { netId } = rootState.metamask
    const gas = 21000
    const { rpcCallRetryAttempt } = rootGetters['metamask/networkConfig']
    return new Promise((resolve, reject) => {
      const refund = async ({ retryAttempt = 0 }) => {
        try {
          const nonce = await web3.eth.getTransactionCount(address)
          let value = await web3.eth.getBalance(address)
          value = BN(value).minus(BN(gas).multipliedBy(gasPrice))

          if (value.isPositive()) {
            const tx = await account.signTransaction({
              to: rootState.metamask.ethAccount,
              gas: numberToHex(gas),
              gasPrice,
              value,
              chainId: netId,
              nonce
            })
            console.log('rawTx', tx.rawTransaction)
            const callParams = {
              method: 'eth_sendRawTransaction',
              params: [tx.rawTransaction],
              from: address
            }
            const txHash = await dispatch('metamask/sendAsync', callParams, {
              root: true
            })
            commit('txHashKeeper/SAVE_TX_HASH', { txName: 'leftOversTx', txHash }, { root: true })
            dispatch('txHashKeeper/runTxWatcher', { txName: 'leftOversTx', txHash }, { root: true })
          }
          resolve(true)
        } catch (e) {
          if (retryAttempt <= rpcCallRetryAttempt) {
            console.error('sendEthLeftOversToUser.refund', e.message)
            retryAttempt++
            setTimeout(() => {
              refund({ retryAttempt })
            }, 1000 * retryAttempt)
          } else {
            return reject(new Error('rpc failed'))
          }
        }
      }
      refund({})
    })
  },
  async multisend({ state, getters, rootGetters, rootState, dispatch, commit }) {
    const provider = rootGetters['metamask/injectedProvider']
    const web3 = new Web3(provider)
    const gasPrice = rootGetters['metamask/gasPrice']
    // eslint-disable-next-line prefer-const
    let { ethAccount } = rootState.metamask
    const { multisenderInstance } = getters
    const token = rootState.token.address.value
    const isNative = token === '0x000000000000000000000000000000000000bEEF'
    const batch = new web3.BatchRequest()
    for (const { params, gas, total } of state.txs) {
      const data = isNative
        ? multisenderInstance.methods.multisendEther(...params).encodeABI()
        : multisenderInstance.methods.multisendToken(...params).encodeABI()
      const airDropETHValue = isNative ? total : 0
      const tx = web3.eth.sendTransaction.request(
        {
          from: ethAccount,
          to: multisenderInstance.address,
          gas: numberToHex(gas + 100000),
          gasPrice,
          value: numberToHex(new BN(state.customerFee).plus(airDropETHValue).toString(10)),
          data
        },
        () => {}
      )
      batch.add(tx)
    }
    const { response } = await batch.execute()
    const txsPromisesBucket = []
    for (const txHash of response) {
      if (txHash) {
        // TODO pass the rest of params
        // eslint-disable-next-line standard/object-curly-even-spacing
        commit('txStorage/SAVE_TX', { txHash, isMetamask: true }, { root: true })
        const receiptStatusPromise = dispatch('txStorage/runTxWatcher', { txHash }, { root: true })
        txsPromisesBucket.push(receiptStatusPromise)
      }
    }
    dispatch('txStorage/runSenderKeyDestroyer', { txsPromisesBucket }, { root: true })
  },
  async buyVip({ state, dispatch, getters, rootGetters, rootState, commit }, { tier }) {
    try {
      const gasPrice = rootGetters['metamask/gasPrice']
      const { ethAccount } = rootState.metamask
      const { multisenderInstance } = getters
      const data = multisenderInstance.methods.buyVip(tier).encodeABI()
      const gas = await multisenderInstance.methods
        .buyVip(tier)
        .estimateGas({ from: ethAccount, value: numberToHex(state.vipCost[tier]) })
      const callParams = {
        method: 'eth_sendTransaction',
        params: [
          {
            from: ethAccount,
            to: multisenderInstance.address,
            gas: numberToHex(gas + 10000),
            gasPrice,
            value: numberToHex(state.vipCost[tier]),
            data
          }
        ],
        from: ethAccount
      }
      const txHash = await dispatch('metamask/sendAsync', callParams, {
        root: true
      })
      commit('txHashKeeper/SAVE_TX_HASH', { txName: 'vipTx', txHash }, { root: true })
      const isSuccess = dispatch(
        'txHashKeeper/runTxWatcher',
        { txName: 'vipTx', txHash },
        { root: true }
      )
      if (isSuccess) {
        commit('SET_CUSTOMER_FEE', 0)
      } else {
        throw new Error('VIP buying failed')
      }
    } catch (e) {
      console.error(e)
    }
  },
  exploreBalances(
    { rootState, dispatch, rootGetters, commit },
    { addresses, txCountPerCall = 1000 }
  ) {
    return new Promise(async (resolve, reject) => {
      try {
        const web3 = rootGetters['metamask/web3']
        const isNative = rootGetters['token/isNative']
        const token = rootState.token.address.value
        const method = isNative ? 'exploreETHBalances' : 'exploreERC20Balances'
        let balances = []
        for (let i = 0; i < addresses.length / txCountPerCall; i++) {
          console.log(i * txCountPerCall, i * txCountPerCall + txCountPerCall)
          commit('SAVE_CURRENT_CHECK', {
            from: i * txCountPerCall,
            to: Math.min(i * txCountPerCall + txCountPerCall, addresses.length),
            method: 'Sorting addresses'
          })
          const addressesToCheck = addresses.slice(
            i * txCountPerCall,
            i * txCountPerCall + txCountPerCall
          )
          const params = isNative ? [addressesToCheck] : [token, addressesToCheck]
          let result = await dispatch('askMultisender', {
            method,
            params,
            gas: numberToHex(8000000),
            web3Method: 'call'
          })
          result = web3.eth.abi.decodeParameter('uint256[]', result)
          balances = balances.concat(result)
        }
        resolve(balances)
      } catch (e) {
        console.error('exploreBalances fail')
        reject(new Error('fetch balances'))
      }
    })
  },
  async _estimateGasPerTransfer({ dispatch, rootState, state, rootGetters }, { addresses }) {
    try {
      const token = rootState.token.address.value
      const allowance = BN(rootState.token.allowance)
      const onePart = allowance.idiv(BN(addresses.length))
      const balances = []
      for (let i = 0; i < addresses.length; i++) {
        balances.push(onePart.toString(10))
      }
      let total = allowance
      const isNative = rootGetters['token/isNative']
      let additionalValue = isNative ? total : 0
      const method = isNative ? 'multisendEther' : 'multisendToken'
      const params = isNative
        ? [addresses, balances]
        : [token, addresses, balances, total.toString(10)]
      const gas = await dispatch('askMultisender', {
        method,
        params,
        web3Method: 'estimateGas',
        value: new BN(state.customerFee).plus(additionalValue).toString(10)
      })
      total = total.minus(onePart)
      additionalValue = isNative ? total : 0
      const params2 = isNative
        ? [addresses.slice(0, -1), balances.slice(0, -1)]
        : [token, addresses.slice(0, -1), balances.slice(0, -1), total.toString(10)]
      const gas2 = await dispatch('askMultisender', {
        method,
        params: params2,
        web3Method: 'estimateGas',
        value: new BN(state.customerFee).plus(additionalValue).toString(10)
      })
      const costOfSingleTransfer = gas - gas2
      return costOfSingleTransfer
    } catch (e) {
      console.error('fail _estimateGasPerTransfer', e)
    }
  },
  async prepareTxs({ state, dispatch, commit }) {
    const allAddresses = await dispatch('organiseAddresses')
    let { existingAddressesList } = allAddresses
    const { freshAddressesList } = allAddresses
    let gasCostPerTransferForFresh = 0
    let gasCostPerTransferForExisting = 0

    let gasLeftovers = 0

    if (freshAddressesList.length) {
      const addresses = [freshAddressesList[0][0]]
      for (let i = 0; i < 9; i++) {
        const randomAddress = toChecksumAddress(
          randomHex(20)
            .substr(2)
            .padStart(40, '0')
        )
        addresses.push(randomAddress)
      }
      gasCostPerTransferForFresh = await dispatch('_estimateGasPerTransfer', { addresses })
      const totalGasForFresh = freshAddressesList.length * Number(gasCostPerTransferForFresh)
      const expectedNumberOfBlocks = Math.ceil(totalGasForFresh / BLOCK_GAS_LIMIT)
      const expectedAddressesCountPerBlock = Math.ceil(BLOCK_GAS_LIMIT / gasCostPerTransferForFresh)

      for (let i = 0; i < expectedNumberOfBlocks; i++) {
        const leftSliceEdge = i * expectedAddressesCountPerBlock
        const rightSliceEdge = leftSliceEdge + expectedAddressesCountPerBlock
        const listToProcess = freshAddressesList.slice(leftSliceEdge, rightSliceEdge)
        const { addresses, balances, total, gas, params } = await dispatch('_constructTxParams', {
          listToProcess
        })
        // last tx
        if (i + 1 === expectedNumberOfBlocks) {
          gasLeftovers = BLOCK_GAS_LIMIT - gas
        }
        commit('SAVE_CURRENT_CHECK', {
          from: i,
          to: '',
          method: 'Preparing transactions'
        })
        commit('PUSH_TX', { addresses, balances, total, gas, params })
      }
    }

    if (existingAddressesList.length) {
      const alreadyPreparedTxs = state.txs.length
      const addresses = [existingAddressesList[0][0], existingAddressesList[0][0]]
      gasCostPerTransferForExisting = await dispatch('_estimateGasPerTransfer', { addresses })
      let totalGasForExisting = existingAddressesList.length * Number(gasCostPerTransferForExisting)
      let addressesToPreviousBlock = 0
      if (gasLeftovers > gasCostPerTransferForExisting) {
        const lastTx = state.txs[state.txs.length - 1]
        commit('REMOVE_LAST_TX')
        addressesToPreviousBlock = Math.floor(gasLeftovers / gasCostPerTransferForExisting)
        const listToProcess = existingAddressesList.slice(0, addressesToPreviousBlock)
        const { addresses, balances, total, gas, params } = await dispatch('_constructTxParams', {
          listToProcess,
          total: BN(lastTx.total),
          addresses: [...lastTx.addresses],
          balances: [...lastTx.balances]
        })
        commit('PUSH_TX', { addresses, balances, total, gas, params })
      }

      existingAddressesList = existingAddressesList.slice(addressesToPreviousBlock)
      totalGasForExisting = existingAddressesList.length * Number(gasCostPerTransferForExisting)

      const expectedNumberOfBlocks = Math.ceil(totalGasForExisting / BLOCK_GAS_LIMIT)
      const expectedAddressesCountPerBlock = Math.ceil(
        BLOCK_GAS_LIMIT / gasCostPerTransferForExisting
      )
      for (let i = 0; i < expectedNumberOfBlocks; i++) {
        const leftSliceEdge = i * expectedAddressesCountPerBlock
        const rightSliceEdge = leftSliceEdge + expectedAddressesCountPerBlock
        const listToProcess = existingAddressesList.slice(leftSliceEdge, rightSliceEdge)
        const { addresses, balances, total, gas, params } = await dispatch('_constructTxParams', {
          listToProcess
        })
        commit('SAVE_CURRENT_CHECK', {
          from: alreadyPreparedTxs + i,
          to: alreadyPreparedTxs + expectedNumberOfBlocks,
          method: 'Preparing transactions'
        })
        commit('PUSH_TX', { addresses, balances, total, gas, params })
      }
    }
  },
  async _constructTxParams(
    { state, dispatch, rootState, rootGetters },
    { listToProcess, total = BN('0'), addresses = [], balances = [] }
  ) {
    const token = rootState.token.address.value
    const isNative = rootGetters['token/isNative']
    const method = isNative ? 'multisendEther' : 'multisendToken'
    listToProcess.forEach(([address, balance]) => {
      addresses.push(address)
      balances.push(balance)
      total = total.plus(balance)
    })
    const additionalValue = isNative ? total : 0
    const params = isNative
      ? [addresses, balances]
      : [token, addresses, balances, total.toString(10)]
    const gas = await dispatch('askMultisender', {
      method,
      params,
      web3Method: 'estimateGas',
      value: new BN(state.customerFee).plus(additionalValue).toString(10)
    })
    total = total.toString(10)
    return { addresses, balances, total, gas, params }
  },
  async findBadAddresses(
    { state, dispatch, getters, commit, rootState, rootGetters },
    { txCountPerCall = 210 }
  ) {
    try {
      const list = getters.airDropList()
      const web3 = rootGetters['metamask/web3']
      const token = rootState.token.address.value
      const isNative = rootGetters['token/isNative']
      const method = isNative ? 'etherFindBadAddresses' : 'tokenFindBadAddresses'
      const badContributors = []
      for (let i = 0; i < list.addresses.length / txCountPerCall; i++) {
        commit('SAVE_CURRENT_CHECK', {
          from: i * txCountPerCall,
          to: Math.min(i * txCountPerCall + txCountPerCall, list.addresses.length),
          method: 'Checking addresses'
        })
        const { addresses, balances, total } = getters.airDropList(
          i * txCountPerCall,
          i * txCountPerCall + txCountPerCall
        )
        const additionalValue = isNative ? total : 0
        const params = isNative
          ? [addresses, balances]
          : [token, addresses, balances, total.toString(10)]

        let result = await dispatch('askMultisender', {
          method,
          params,
          web3Method: 'call',
          gas: numberToHex(8000000),
          value: new BN(state.customerFee).plus(additionalValue).toString(10)
        })
        result = web3.eth.abi.decodeParameters(['address[]', 'uint256[]'], result)
        result[1].reduce((accumulator, balance, i) => {
          if (!balance.isZero()) {
            accumulator.push({
              address: result[0][i],
              balance
            })
          }
          return accumulator
        }, badContributors)
      }

      commit('SAVE_BAD_CONTRIBUTORS', badContributors)
      return badContributors.length > 0
    } catch (e) {
      console.error('findBadAddresses fail', e)
      if (txCountPerCall > 50) {
        return dispatch('findBadAddresses', { txCountPerCall: txCountPerCall - 50 })
      } else {
        console.error('findBadAddresses fail. It seems the token is completely broken.', e)
      }
    }
  },
  removeBadAddresses({ state, commit }) {
    commit('REMOVE_ADDRESSES_FROM_LIST')
    const badTotal = state.badContributors.reduce((accumulator, { balance }) => {
      accumulator = accumulator.plus(balance)
      return accumulator
    }, BN('0'))
    const newTotal = BN(state.totalTokens).minus(badTotal)
    commit('SAVE_TOTAL_TOKENS', newTotal.toString())
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
