const EIP712Domain = [
  { name: 'name', type: 'string' },
  { name: 'version', type: 'string' },
  { name: 'chainId', type: 'uint256' },
  { name: 'verifyingContract', type: 'address' },
  { name: 'salt', type: 'bytes32' }
]
const Authorization = [
  { name: 'authorizedSigner', type: 'address' },
  { name: 'expiration', type: 'uint256' }
]

function main({ name, version, chainId, verifyingContract, salt }) {
  const domainData = {
    name,
    version,
    chainId,
    verifyingContract,
    salt
  }

  function getAuthorizationData({ authorizedSigner, expiration }) {
    const authorizationMessage = {
      authorizedSigner,
      expiration
    }
    const authorizationData = {
      types: {
        EIP712Domain,
        Authorization
      },
      domain: domainData,
      primaryType: 'Authorization',
      message: authorizationMessage
    }
    return authorizationData
  }

  return {
    getAuthorizationData,
    Schemas: {
      EIP712Domain,
      Authorization
    }
  }
}

export default main
