export default ({ store, isHMR, app }, inject) => {
  inject('preventMultitabs', main)
}

function main(store) {
  const id = Date.now()
  window.id = id
  window.localStorage.multisenderKey = id
  const onLocalStorageEvent = function(e) {
    if (e.key === 'multisenderKey') {
      localStorage.page_available_multisender = Date.now()
    }
    // eslint-disable-next-line eqeqeq
    if (
      e.key === 'page_available_multisender' &&
      window.id.toString() === localStorage.multisenderKey
    ) {
      window.multipleTabsDetected = true
      window.onbeforeunload = null
      window.alert(
        'Multiple tabs opened. Your page will be closed. Please only use single instance of https://multisender.app'
      )
      window.location = 'https://twitter.com/rstormsf/status/1121131647066198016'
    }
  }

  window.addEventListener('storage', onLocalStorageEvent, false)
}
