/* eslint-disable no-console */
import ABI from '@/abis/ERC20.abi.json'
import BN from 'bignumber.js'

const {
  isAddress,
  hexToNumber,
  hexToNumberString,
  toChecksumAddress,
  numberToHex
} = require('web3-utils')

export const state = () => ({
  address: {
    value: '',
    formValidationClass: '',
    symbol: ''
  },
  tokenBalance: '0',
  allowance: '0',
  decimals: 0
})

export const mutations = {
  SET_TOKEN_ADDRESS(state, { address, formValidationClass = '', label, symbol }) {
    state.address = {
      value: address,
      formValidationClass,
      label,
      symbol
    }
  },
  SET_DECIMALS(state, decimals) {
    state.decimals = decimals
  },
  SET_TOKEN_BALANCE(state, tokenBalance) {
    state.tokenBalance = tokenBalance
  },
  SET_TOKEN_ALLOWANCE(state, allowance) {
    state.allowance = allowance
  }
}

export const getters = {
  tokenInstance: (state, getters, rootState, rootGetters) => {
    const address = state.address.value
    let tokenInstance = {}
    if (isAddress(address)) {
      const web3 = rootGetters['metamask/web3']
      tokenInstance = new web3.eth.Contract(ABI, toChecksumAddress(state.address.value))
    }
    return tokenInstance
  },
  toDecimals: state => (value, customDecimals, customFixed) => {
    customFixed = customFixed || 4
    let { decimals } = state
    decimals = customDecimals || decimals
    const bn = new BN(value).div(new BN('10').pow(new BN(decimals)))
    return bn.toFixed(customFixed)
  },
  isNative: state => {
    return state.address.value === '0x000000000000000000000000000000000000bEEF'
  },
  getTokenSymbol: state => {
    return state.address.symbol
  }
}

export const actions = {
  setTokenAddress({ dispatch, commit, rootState, rootGetters }, { address, label, symbol }) {
    const isAddressValid = address.length >= 42 && isAddress(address)
    commit('SET_TOKEN_ADDRESS', {
      address,
      formValidationClass: isAddressValid ? 'is-success' : 'is-danger',
      label,
      symbol
    })
    if (isAddressValid) {
      if (address === '0x000000000000000000000000000000000000bEEF') {
        const balance = rootState.metamask.ethBalance
        commit('SET_DECIMALS', 18)
        commit('SET_TOKEN_BALANCE', balance)
        commit('SET_TOKEN_ALLOWANCE', balance)
        return
      }
      dispatch('_fetchDecimals')
      dispatch('_fetchTokenBalance')
      dispatch('_fetchAllowance')
    }
  },
  async _fetchDecimals({ commit, dispatch }) {
    const decimals = await dispatch('askToken', {
      method: 'decimals',
      web3Method: 'call'
    })
    commit('SET_DECIMALS', hexToNumber(decimals))
  },
  async _fetchTokenBalance({ commit, dispatch, rootState }) {
    const { ethAccount } = rootState.metamask
    const balance = await dispatch('askToken', {
      method: 'balanceOf',
      params: [ethAccount],
      web3Method: 'call'
    })
    commit('SET_TOKEN_BALANCE', hexToNumberString(balance))
  },
  async _fetchAllowance({ commit, dispatch, rootState, rootGetters }) {
    const { multisenderContractAddress } = rootGetters['metamask/networkConfig']
    const { ethAccount } = rootState.metamask
    const allowance = await dispatch('askToken', {
      method: 'allowance',
      params: [ethAccount, multisenderContractAddress],
      web3Method: 'call'
    })
    commit('SET_TOKEN_ALLOWANCE', hexToNumberString(allowance))
  },
  async sendApprove({ state, rootState, getters, rootGetters, dispatch, commit }, approveSize) {
    try {
      const gasPrice = rootGetters['metamask/gasPrice']
      const { multisenderContractAddress } = rootGetters['metamask/networkConfig']
      const { ethAccount } = rootState.metamask
      const { tokenInstance } = getters
      const data = tokenInstance.methods
        .approve(multisenderContractAddress, approveSize)
        .encodeABI()
      const gas = await tokenInstance.methods
        .approve(multisenderContractAddress, approveSize)
        .estimateGas({ from: ethAccount })
      const callParams = {
        method: 'eth_sendTransaction',
        params: [
          {
            from: ethAccount,
            to: tokenInstance.address,
            gas: numberToHex(gas + 100000),
            gasPrice,
            value: 0,
            data
          }
        ],
        from: ethAccount
      }
      const txHash = await dispatch('metamask/sendAsync', callParams, {
        root: true
      })
      commit('txHashKeeper/SAVE_TX_HASH', { txName: 'approveTx', txHash }, { root: true })
      const isSuccess = dispatch(
        'txHashKeeper/runTxWatcher',
        { txName: 'approveTx', txHash },
        { root: true }
      )
      if (isSuccess) {
        const currentValue = state.allowance
        const allowance = await dispatch('askTokenForChanges', {
          params: [ethAccount, multisenderContractAddress], // owner, spender
          method: 'allowance',
          currentValue
        })
        commit('SET_TOKEN_ALLOWANCE', hexToNumberString(allowance))
        const areBadAddresses = await dispatch('distribution/findBadAddresses', {}, { root: true })
        let nextPage
        if (areBadAddresses) {
          nextPage = 'badAddresses'
        } else {
          await dispatch('distribution/prepareTxs', {}, { root: true })
          nextPage = 'summary'
        }
        dispatch('router/setNextPage', { nextPage }, { root: true })

        return true
      } else {
        throw new Error('Approve failed')
      }
    } catch (e) {
      console.error(e)
      return false
    }
  },
  askToken({ dispatch, getters }, { method, params = [], web3Method }) {
    const { tokenInstance } = getters
    const data = tokenInstance.methods[method](...params).encodeABI()
    return dispatch(
      'metamask/callWeb3',
      { data, to: tokenInstance.address, web3Method },
      { root: true }
    )
  },
  askTokenForChanges({ dispatch, getters, rootGetters }, { method, params = [], currentValue }) {
    const { tokenInstance } = getters
    const { rpcCallRetryAttempt } = rootGetters['metamask/networkConfig']
    const data = tokenInstance.methods[method](...params).encodeABI()
    currentValue = new BN(currentValue)
    return new Promise(async (resolve, reject) => {
      const checkForChanges = async ({ retryAttempt = 0, currentValue, rpcCallRetryAttempt }) => {
        retryAttempt++
        const newValue = await dispatch(
          'metamask/callWeb3',
          { data, to: tokenInstance.address, web3Method: 'call' },
          { root: true }
        )
        if (new BN(newValue).isEqualTo(currentValue)) {
          if (retryAttempt > rpcCallRetryAttempt) {
            return reject(new Error(`return value of '${method}' method was not changed`))
          }
          setTimeout(async () => {
            await checkForChanges({ retryAttempt, currentValue, rpcCallRetryAttempt })
          }, 1000 * retryAttempt)
        } else {
          return resolve(newValue)
        }
      }
      await checkForChanges({ currentValue, rpcCallRetryAttempt })
    })
  }
}
