/* eslint-disable no-console */

export const state = () => ({
  nextPage: 'approve'
})

export const mutations = {
  SET_NEXT_PAGE(state, nextPage) {
    state.nextPage = nextPage
  }
}

export const actions = {
  setNextPage({ commit }, { nextPage }) {
    if (
      ['approve', 'badAddresses', 'index', 'statistic', 'summary', 'vip'].indexOf(nextPage) === -1
    ) {
      throw Error(`Invalid route ${nextPage}`)
    }
    commit('SET_NEXT_PAGE', nextPage)
  },
  goToTheNextPage({ state }) {
    this._vm.$nuxt.$router.push({
      path: `/${state.nextPage}`
    })
  },
  goToIndex() {
    this._vm.$nuxt.$router.push({
      path: '/'
    })
  },
  setNextPageAndGo({ state, dispatch }, { nextPage }) {
    dispatch('setNextPage', { nextPage })
    dispatch('goToTheNextPage', {})
  }
}
