<template>
  <footer class="footer">
    <div class="container">
      <div class="level">
        <div class="level-left">
          <div class="level-item footer__copyright">
            <svg class="footer__logo" xmlns="http://www.w3.org/2000/svg" width="18" height="25">
              <path
                fill="#9CD8FF"
                fill-rule="evenodd"
                d="M10 16v-2a6 6 0 1 0 0-12V0a8 8 0 0 1 0 16zm3-8a3 3 0 0 1-3 3V5a3 3 0 0 1 3 3zM2 17a6 6 0 0 0 6 6v2A8 8 0 0 1 8 9v2a6 6 0 0 0-6 6zm3 0a3 3 0 0 1 3-3v6a3 3 0 0 1-3-3z"
              />
            </svg>
            Token MultiSender v2.07.8 {{ new Date().getFullYear() }}
          </div>
        </div>
        <div class="level-right">
          <div class="level-item multi-address">
            <div class="multi-address__name">
              MultiSender Address ({{ networkConfig.networkName }}):
            </div>
            <a
              class="multi-address__value"
              target="_blank"
              :href="`${explorerBaseUrl}/address/${networkConfig.multisenderContractAddress}`"
            >
              {{ networkConfig.multisenderContractAddress }}
            </a>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('metamask', ['networkConfig']),
    explorerBaseUrl() {
      if (process.client) {
        return new window.URL(this.networkConfig.explorerUrl.tx).origin
      }
      return this.networkConfig.explorerUrl.tx.slice(0, -4)
    }
  }
}
</script>
